@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bolditalic.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic.woff") format("woff");
  font-weight: 700;
  font-style: italic;
}
@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-italic.woff2") format("woff2"), url("../fonts/bloggersans-italic.woff") format("woff");
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-lightitalic.woff2") format("woff2"), url("../fonts/bloggersans-lightitalic.woff") format("woff");
  font-weight: 300;
  font-style: italic;
}
@font-face {
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-mediumitalic.woff2") format("woff2"), url("../fonts/bloggersans-mediumitalic.woff") format("woff");
  font-weight: 500;
  font-style: italic;
}
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Blogger Sans", arial, sans-serif;
  font-weight: 300;
  line-height: 22px;
  font-size: 16px;
  color: #444444;
}
* {
  box-sizing: inherit;
}
* *::after {
  box-sizing: inherit;
}
* *::before {
  box-sizing: inherit;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.container {
  width: 100%;
  padding-left: 25px;
  padding-right: 25px;
}
.site-header {
  position: relative;
  background-color: #161c36;
  color: #ffffff;
  background-image: url("../../img/globe-back-mobile@1x.jpg");
  background-size: 100% 569px;
  background-position: center top;
  background-repeat: no-repeat;
}
.site-header::after {
  content: " ";
  position: absolute;
  top: 223px;
  right: 48px;
  width: 192px;
  height: 255px;
  background-image: url(../img/traveller-illustration-mobile@1x.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.site-header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-top: 27px;
  padding-bottom: 27px;
}
.site-header__list {
  display: none;
}
.site-header__toggle {
  width: 35px;
  padding: 5px;
  margin-right: -5px;
  margin-top: -5px;
  background-color: transparent;
  border: none;
}
.site-header__toggle::before,
.site-header__toggle::after {
  content: " ";
  position: relative;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #ffffff;
  margin-bottom: 4px;
  border-radius: 3px;
}
.site-header__toggle::after {
  margin-bottom: 0;
}
.site-header__headline {
  width: 240px;
  margin: 12px auto 247px auto;
  text-align: center;
}
.site-header__help {
  position: relative;
  display: flex;
  margin: 0 -25px 0 -25px;
  padding: 0 25px 0 25px;
  z-index: 1;
}
.site-header__help::before {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 22px;
  background-color: #161c36;
  z-index: -1;
}
.site-header__reviews {
  position: relative;
  background-color: #161c36;
  margin: 0 -25px 0 -25px;
  padding: 26px 25px 41px 25px;
}
.site-header__comment {
  width: 155px;
  margin: 0 0 0 24px;
  color: #ffffff;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  text-align: left;
}
.site-header__author {
  display: block;
  width: 155px;
  margin-top: 18px;
  margin-left: 24px;
  color: #ffffff;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  line-height: 22px;
}
.site-header__work {
  position: relative;
  margin-left: -25px;
  margin-right: -25px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 39px;
  background-color: #192144;
}
.site-header__work::before {
  content: " ";
  position: absolute;
  top: -9px;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: #161c36;
}
.h1 {
  font-size: 36px;
  line-height: 40px;
  font-weight: 700;
}
.h2 {
  font-size: 24px;
  line-height: 24px;
  color: #ff8d30;
  font-weight: 700;
}
.contact-us {
  list-style: none;
  padding: 0;
  margin: 0;
}
.contact-us--row {
  display: flex;
  flex-wrap: wrap;
}
.contact-us__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #ffbd30;
  border-radius: 50%;
  transition: 0.2s;
}
.contact-us__link:focus .contact-us__img {
  opacity: 0.3;
}
.contact-us__link--phone {
  background-color: #ff8d30;
}
.contact-us__link--phone:hover {
  background-color: #f26926;
}
.contact-us__link--phone:focus {
  background-color: #f26926;
}
.contact-us__link--mail:hover {
  background-color: #ff8d30;
}
.contact-us__link--mail:focus {
  background-color: #ff8d30;
}
.how-work__list {
  padding: 0;
  margin: 0;
  list-style: none;
  counter-reset: item;
}
.how-work__item {
  position: relative;
  padding: 11px 38% 33px 9%;
  font-weight: 700;
  color: #161c36;
  font-size: 18px;
  line-height: 18px;
  background-color: #ff8d30;
}
.how-work__item::after {
  content: counter(item);
  counter-increment: item;
  position: absolute;
  right: 22px;
  top: 11px;
  font-size: inherit;
  line-height: inherit;
  color: #ffffff;
}
.how-work__item::before {
  content: " ";
  position: absolute;
  top: -10px;
  left: 0;
  display: block;
  width: 100%;
  height: 10px;
  background-color: #ff8d30;
  border-radius: 10px 10px 0 0;
}
.how-work__item--idea {
  background-color: #ffa530;
}
.how-work__item--idea::before {
  background-color: #ffa530;
}
.how-work__item--similar {
  background-color: #ffbd30;
}
.how-work__item--similar::before {
  background-color: #ffbd30;
}
.how-work__item--together {
  background-color: #ffd74b;
  border-radius: 0 0 10px 10px;
  padding-bottom: 22px;
}
.how-work__item--together::before {
  background-color: #ffd74b;
}
.button {
  display: inline-block;
  padding: 5px 39px;
  vertical-align: baseline;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
  color: #161c36;
  text-decoration: none;
  background-color: #ffd74b;
  border-radius: 20px;
  transition: 0.2s;
}
.button:hover {
  background-color: #ffbd30;
}
.button:focus {
  background-color: #ffbd30;
  color: rgba(22, 28, 54, 0.3);
}
.about-service {
  position: relative;
  padding-top: 32px;
  padding-bottom: 81px;
  color: #ffffff;
  background-color: #192144;
  border-radius: 0 0 30px 30px;
  overflow: hidden;
  z-index: 1;
}
.about-service::before {
  content: " ";
  position: absolute;
  left: -22px;
  top: 1px;
  display: block;
  width: 132px;
  height: 132px;
  background-color: #141b3b;
  border-radius: 20px;
  z-index: -1;
}
.about-service::after {
  content: " ";
  position: absolute;
  top: 1px;
  left: 50%;
  display: block;
  width: 292px;
  height: 352px;
  background-image: url("../img/about-mobile@1x.png");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
  transform: translate(-49%, 0);
}
.about-service__headline {
  margin: 0  0 20px 25px;
}
.about-service__description {
  width: 220px;
  margin: 20px auto 20px auto;
  color: inherit;
  font-weight: 300;
  line-height: 22px;
  font-size: 16px;
}
.about-service__promo {
  width: 221px;
  margin: 19px auto 0;
  font-weight: 700;
}
